import React, {useEffect} from "react";
import {Link} from "gatsby"
import {useTranslation} from "react-i18next";

export default function NavigationMobile() {
    const {i18n, t} = useTranslation();

    function changeLanguage(value) {
        i18n.changeLanguage(value);
    }

    useEffect(() => {
        require('../../scss/style.scss');
        let nav_el = document.querySelector(".site-navigation");
        let body = document.body;
        body.classList.add("page-loaded");
        document.querySelector(".hamburger").addEventListener('click', function () {
            if (nav_el.classList.contains("active")) {
                body.classList.toggle("overflow");
                nav_el.classList.remove("active");
                nav_el.style.cssText = "transition-delay: 0.5s";
                document.querySelector(".site-navigation .layer").style.cssText = "transition-delay: 0.3s";
                document.querySelector(".site-navigation .inner").style.cssText = "transition-delay: 0s";
            } else {
                nav_el.classList.add("active");
                body.classList.toggle("overflow");
                document.querySelector(".site-navigation.active").style.cssText = "transition-delay: 0s";
                document.querySelector(".site-navigation.active .layer").style.cssText = "transition-delay: 0.2s";
                document.querySelector(".site-navigation.active .inner").style.cssText = "transition-delay: 0.7s";
            }
            this.classList.toggle("is-opened-navi");
        });
        document.querySelector(".nav-links").addEventListener('click', function () {
            document.querySelector(".hamburger").click();
        });
    }, []);

    return (
        <>
            <nav className="site-navigation">
                <div className="layer"/>
                <div className="inner">
                    <ul className="nav-links">
                        <li><Link to="/">{t('discover')}</Link></li>
                        <li><Link to="/about-us/">{t('about_us')}</Link></li>
                        <li><Link to="/our-partners/">{t('our_partners')}</Link></li>
                        <li><Link to="/portfolio/">{t('portfolio')}</Link></li>
                        <li><Link to="/blogs/">{t('blogs')}</Link></li>
                        <li><Link to="/career/">{t('career')}</Link></li>
                        <li><Link to="/contact-us/">{t('contact_us')}</Link></li>

                        {/*<li>*/}
                        {/*    <button className={`${i18n.language === 'en' ? "hidden" : ""}`} onClick={changeLanguage('en')}>EN</button>*/}
                        {/*</li>*/}
                        {/*<li>*/}
                        {/*    <button className={`${i18n.language === 'lv' ? "hidden" : ""}`} onClick={changeLanguage('lv')}>LV</button>*/}
                        {/*</li>*/}
                    </ul>
                </div>
            </nav>

            <aside className="left-side">
                <div className="hamburger" id="hamburger">
                    <div className="hamburger__line hamburger__line--01">
                        <div className="hamburger__line-in hamburger__line-in--01"/>
                    </div>
                    <div className="hamburger__line hamburger__line--02">
                        <div className="hamburger__line-in hamburger__line-in--02"/>
                    </div>
                    <div className="hamburger__line hamburger__line--03">
                        <div className="hamburger__line-in hamburger__line-in--03"/>
                    </div>
                    <div className="hamburger__line hamburger__line--cross01">
                        <div className="hamburger__line-in hamburger__line-in--cross01"/>
                    </div>
                    <div className="hamburger__line hamburger__line--cross02">
                        <div className="hamburger__line-in hamburger__line-in--cross02"/>
                    </div>
                </div>
            </aside>
        </>
    );
}
