import React from "react"
import NavigationMobile from "./NavigationMobile";
import {Link} from "gatsby"
import {useTranslation} from "react-i18next";

const Navigation = ({pageNr, isPost}) => {
    const {t, i18n} = useTranslation();

    return (
        <>
            {isPost !== true &&
                <div className="absolute css--page-number top-12 left-1/10" style={{zIndex: "20"}}>{pageNr}</div>
            }

            <NavigationMobile/>

            <div className="css--navbar-logo">
                <Link to="/">
                    <img src={"/images/brand/logo_text_White-full.svg"} style={{opacity: 0.2}} className="pt-5 md:pt-0 w-full" alt="Kalmars Logo"/>
                </Link>
            </div>
            <nav className="fixed text-center hidden md:block py-4 px-6 bg-transparent w-full" style={{zIndex: "300"}}>
                <div className="w-4/5 mx-auto flex justify-evenly relative">
                    <Link to="/" className={(pageNr === '01' ? "css--navigation-active" : "") + " text-white font-bold"}>
                        {t('discover')}
                    </Link>
                    <Link to="/about-us/" className={(pageNr === '02' ? "css--navigation-active" : "") + " text-white font-bold"}>
                        {t('about_us')}
                    </Link>
                    <Link to="/portfolio/" className={(pageNr === '03' ? "css--navigation-active" : "") + " text-white font-bold"}>
                        {t('portfolio')}
                    </Link>
                    <Link to="/blogs/" className={(pageNr === '04' ? "css--navigation-active" : "") + " text-white font-bold"}>
                        {t('blogs')}
                    </Link>
                    <Link to="/career/" className={(pageNr === '05' ? "css--navigation-active" : "") + " text-white font-bold"}>
                        {t('career')}
                    </Link>
                    <Link to="/contact-us/" className={(pageNr === '06' ? "css--navigation-active" : "") + " text-white font-bold"}>
                        {t('contact_us')}
                    </Link>
                    {/*<div className="flex justify-end">*/}
                    {/*    <button className={`${i18n.language === 'en' ? "hidden" : ""}`} onClick={changeLanguage('en')}>EN</button>*/}
                    {/*    <button className={`${i18n.language === 'lv' ? "hidden" : ""}`} onClick={changeLanguage('lv')}>LV</button>*/}
                    {/*</div>*/}
                </div>
            </nav>
        </>
    )
}

export default Navigation;
